@import "./src/sass/libs/settings";
@media screen and (max-width: 989px){
  .expanded a{
    color: $muted-color;
  }
  .expanded a:hover {
    //color: $border-color;
    color: $accent-color;
  }
    a li{
      list-style: none;
    }
  .menu-item{
    border-bottom: 1px solid #DEE2E6FF;

    &:first-child{
      border-top: 1px solid #DEE2E6FF;
    }
  }

  a{
    font-family: $primary-font;
    text-transform: uppercase;
    font-weight: 400;
    display: block;
    padding: 10px 20px;
  }

  small{
    display: block;
    color: $muted-color;
  }
}


@media screen and (min-width: 990px) {
  .expanded .menu{
    display: none;
  }
}
